@import url(https://fonts.googleapis.com/css2?family=Bricolage+Grotesque:opsz,wght@12..96,300&family=Cinzel&family=Golos+Text:wght@500&family=Source+Code+Pro&family=VT323&display=swap);





.nav {
    background-color: rgba(0, 0, 0, 0.5); 
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 0 1rem;
    font-size: 2rem;
    
}


.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 1rem;
    font-size: 1rem;
}


.nav a {
    color: inherit;
    text-decoration: none;
    display: flex;
}

.nav li:hover {
    background-color: #777;
}


.nav li.active {
    background-color: #555;
}



.active {
    background-color: #ddd; /* or any other styling you prefer */
  }


.contact-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  padding-bottom: 210px;
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 20px;
}

label {
  font-size: 16px;
  margin-bottom: 10px;
  display: block;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

textarea {
  resize: vertical;
  min-height: 100px;
}

.submit-button {
  background-color: #4CAF50;
  color: white;
  padding: 15px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.submit-button:hover {
  background-color: #45a049;
}




/* Footer.css */
.sticky-footer {
    width: 100%;

    padding: 10px 0; 
    background-color: rgba(0, 0, 0, 0.5); 
    bottom: 0; 
    box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.1);
  }
  
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .social-media-links a {
    margin: 0 15px; 
    text-decoration: none;
    color: #333; 
    font-weight: bold;
  }
  



* {
    box-sizing: border-box;

}


body {
    margin: 0;
    font-family: 'Source Code Pro', monospace;
}


h1{ 
    text-align: center;
    padding: 30px;
}

p {
    
        text-align: center;
        padding: 30px;
    
}


.resume-container {
    width: 80%;
    margin: 0 auto;
    padding: 40px; /* Adjust as needed */
    box-sizing: border-box;
    padding-bottom: 200px;
  }
  

.sideNote {
    color: rgb(106, 105, 105);
    opacity: 50%;
    list-style-type: none;
}


.video-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1; /* To place the video below other content */
  }
  
  .background-video {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    opacity: 60%;
  }
